.friendList {
  width: 370px;
  background: transparent;
  box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 1px 3px rgba(0, 0, 0, 0.12);
  border-radius: 0px 0px 4px 4px;
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
}
.online {
  display: block;
  width: 10px;
  height: 10px;
  background-color: green;
  border-radius: 50%;
  margin-left: 10px;
}

.offline {
  display: block;
  height: 10px;
  width: 10px;
  background-color: red;
  border-radius: 50%;
  margin-left: 10px;
}

.friendItem {
  display: flex;
  align-items: center;
  padding: 5px;
  font-size: 20px;
  font-weight: 500;
  background-color: #ffffff;
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.2), 0 1px 1px rgba(0, 0, 0, 0.14),
    0 1px 3px rgba(0, 0, 0, 0.12);
  border-radius: 0 0 4px 4px;
  margin-bottom: 10px;
}

.avatar {
  width: 80px;
  margin-right: 30px;
}

.name {
  text-align: center;
}
